* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
@import url("https://fonts.googleapis.com/css2?family=Montserrat&display=swap");
body {
  background-color: #0f112f;
  color: white;
  font-family: "Montserrat", sans-serif;
  padding: 0px 20px;
}

@media (max-width: 480px) {
  .navbar {
    padding: 30px 0px;
  }
  /* #269ed6 */

  .balanceInfo {
    background: linear-gradient(
      89.56deg,
      rgba(15, 17, 47, 0) 4.9%,
      #29bfce 120.45%
    );
    border-radius: 14px;
    padding: 15px 0px;
    margin: 10px 0px;
    display: flex;
    align-items: center;
  }
  .crystle {
    height: 50px;
    margin-right: 10px;
  }
  .totalwalletBalance {
    /* font-size: x-large;
    font-family: "Lato"; */
    font-style: normal;
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    display: flex;
    align-items: center;

    color: #ffffff;
  }
  .walletBalance {
    font-style: normal;
    font-weight: 400;
    font-size: 12px;
    line-height: 14px;
    display: flex;
    align-items: center;

    color: #ffffff;
  }

  .back {
    height: 20px;
    transform: rotate(180deg);
  }
  .btnBox {
    font-size: 2px;
    display: flex;
    justify-content: end;
  }
  .plusIcon {
    height: 25px;
    margin-right: 5px;
  }
  
  .btn {
    display: flex;
    justify-content: center;
    align-items: center;
    color: #29BFCE;
    background-color: transparent;
    padding: 2px 10px;
    border: 2px solid #29BFCE;
    border-radius: 20px;
    font-weight: 500;
    font-size: x-small;
  }
  .transactions {
    margin: 20px 0px;
    font-size: smaller;
  }
  .transactionsinfo {
    padding: 20px 0px;
    display: flex;
    justify-content: space-between;
  }
  .details2 {
    text-align: end;
  }
  .title {
    padding: 3px 0px;
  }
  .date {
    padding-top: 3px;
    font-size: x-small;
    color: rgb(194, 189, 189);
  }
  .paid {
    padding: 3px 0px;
  }
  .balance {
    font-size: x-small;
    padding-top: 3px;
    color: rgb(194, 189, 189);
  }
  .increase{
    background: #132248;
    border-radius: 19px;
  }
}
